// base.scss
// Colors
$color-primary: #E3EEDE;
$color-dark:    #333333;
$color-medium:  #999999;
$color-light:   #CCCCCC;
$color-text:    #BABABA;


$brand-color1: #78B5CA;
$brand-color2: #CA767F;
$brand-color3: #29ADA8;
$brand-color4: #F9D085;
$brand-color5: #6B81B2;

// Oasis old colors
$brand-color7: #415A68;
$brand-color8: #778D1D;

$brand-accent: $brand-color1;
$brand-accent: #7B8FC7; // purple
$brand-accent: #50E3C2; // seafoam

// Text
$text-base: 16px;
$text-small: 0.8rem;

// Screens
$screen-sms: 360px;
$screen-sm: 768px;
$screen-md: 960px;
$screen-lg: 1100px;
$screen-xl: 1400px;


// Typography
$font-family-sans-serif:          "League Gothic", "league-gothic", Helvetica, Arial, sans-serif;
$font-family-serif:               "Museo Slab", "museo-slab", "Georgia", "Times New Roman", Times, serif;
$font-family-monospace:            Menlo, Monaco, Consolas, "Courier New", monospace;
$font-family-base:                 $font-family-serif;
$font-base-color: 								#8a8e91;


// Media queers
// @include screen-xs { }
@mixin screen-xs {
	@media (max-width: #{$screen-sm - 1}) {
		@content;
	}
}
// @include screen-sm-5 { }
// Wacked out media querie I need
@mixin screen-sms {
	@media (min-width: #{$screen-sms}) {
		@content;
	}
}
// @include screen-sm { }
@mixin screen-sm {
	@media (min-width: #{$screen-sm}) {
		@content;
	}
}
// @include screen-md { }
@mixin screen-md {
	@media (min-width: #{$screen-md}) {
		@content;
	}
}
// @include screen-lg { }
@mixin screen-lg {
	@media (min-width: #{$screen-lg}) {
		@content;
	}
}
// @include screen-lg { }
@mixin screen-xl {
	@media (min-width: #{$screen-xl}) {
		@content;
	}
}



// Vertically align items
// @include vertical-align()
@mixin vertical-align {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

// Generic transition
// @include transition(0.2s)
@mixin transition($time) {
	-webkit-transition: all $time ease-in-out;
	   -moz-transition: all $time ease-in-out;
	        transition: all $time ease-in-out;
}

// Opacity mixin
// @include opacity(0.2s)
@mixin opacity($time) {
	-webkit-transition: opacity $time ease-in-out,visibility 0s 0s linear,top $time ease-in-out;
	   -moz-transition: opacity $time ease-in-out,visibility 0s 0s linear,top $time ease-in-out;
	    -ms-transition: opacity $time ease-in-out,visibility 0s 0s linear,top $time ease-in-out;
	     -o-transition: opacity $time ease-in-out,visibility 0s 0s linear,top $time ease-in-out;
	        transition: opacity $time ease-in-out,visibility 0s 0s linear,top $time ease-in-out;
}

// Generic transform
@mixin transform($transforms) {
	   -moz-transform: $transforms;
	     -o-transform: $transforms;
	    -ms-transform: $transforms;
	-webkit-transform: $transforms;
          transform: $transforms;
}

// Rotate
@mixin rotate ($deg) {
	@include transform(rotate(#{$deg}deg));
}

// Scale
@mixin scale($scale) {
	@include transform(scale($scale));
}
// Translate
@mixin translate ($x, $y) {
	@include transform(translate($x, $y));
}
// Skew
@mixin skew ($x, $y) {
	@include transform(skew(#{$x}deg, #{$y}deg));
}


