//   background: black;
//   @include animation('slide-down 5s 3');
// }

@include keyframes(slide-down) {
	0% {
		top: initial;
		height: 50%;
	}

	100% {
		top: 0;
		height: 100%;
	}
}

@include keyframes(slide-up) {
	0% {
		top: initial;
		height: 50%;
	}

	100% {
		position: absolute;
		top: 0;
		// transform: translateY(-100%);
		height: 100%;
	}
}



.helpPop {
	display: inline-block;

	position: fixed;
	bottom: 15px;
	right: 15px;
	z-index: 1100;

	padding: 12px 20px;
	color: white;
	background: $brand-color1;
	border-radius: 50%;

	overflow: hidden;
	white-space: nowrap;

	visibility: hidden;
	opacity: 0;

	-webkit-transition: opacity .3s 0s, visibility 0s .3s;
	-moz-transition: opacity .3s 0s, visibility 0s .3s;
	transition: opacity .3s 0s, visibility 0s .3s;

	&:active, &:hover, &:focus {
		color: darken($brand-color1, 15%);
	}

	&.is-visible, &.fade-out, .no-touch &:hover {
		-webkit-transition: opacity .3s 0s, visibility 0s 0s;
		-moz-transition: opacity .3s 0s, visibility 0s 0s;
		transition: opacity .3s 0s, visibility 0s 0s;
	}

	&.is-visible {
		visibility: visible;
		opacity: 0.8;
	}

	&.fade-out {
		// make less visible
		opacity: 0;
	}

	.no-touch &:hover {
		background-color: rgba(#333, 1);
		opacity: 1;
	}

}


